.team-logo {
    width: 8rem;
    margin: 1rem 0 0 1.6rem;
}

.nowrap {
    flex-wrap: nowrap !important;
}

.broadcasting-radial {
    font-family: "Helvetica", "Arial", sans-serif;
    font-size: 1.25rem;

    text:nth-child(2n) {
        font-size: .7rem;
    }

    text:nth-child(2), text:nth-child(8) {
        transform: translateY(.5rem); 
    }
}