@import './styles/palette.scss';
@import "~react-vis/dist/style";

.app {
  background-color: $secondary;
  height: 100vh;
  padding: 0 5px;

  .background {
    height: 100%;
    width: 100%;

    img {
      height: 100%;
      width: 100%;
    }
  }
}

.app-link {
  color: #61dafb;
}

.flex {
  display: flex;
}

.align-right {
  margin-left: auto;
}

.text-center {
  text-align: center;
}

h3 {
  margin-top: 0;
  margin-bottom: 1rem;
}

.pad-all-sm { padding: .5rem; }
.pad-md { padding: 1rem; }
.pad-left-sm { padding-left: .5rem; }
.pad-left-md { padding-left: 1rem; }
.pad-left-lg { padding-left: 2rem; }
.pad-left-xl { padding-left: 3rem; }
.pad-left-xxl { padding-left: 4rem; }

.marg-bottom-sm { margin-bottom: .5rem; }